import {
	get,
	post,
	config
} from '@/utils/api.js';
export default {
	name: 'hbList',
	data() {
		return {
			txnum:'',
			currentPage: 1,
			count:10,
			nextPage: false,
			hbMore: false,
			more:false,
			noData: false,

			imgUrl: config.staticUrl,
			moneyList: [],
			restMoney: 0.00,
			totalMoney: 0.00,
			needTx:false,
			hasPhone: false,
			mobileToast:false,//手机号再次授权

			successToast: false,
			failToast: false,
			hbListRes: '',


		}
	},
	onLoad() {
		const phone = uni.getStorageSync('tel');
		console.log(phone);
		if(phone){
			this.hasPhone = true
		} else {
			this.hasPhone = false
		}
		this.hbList();
	},
	onShow(){
		if(this.needTx) {
			this.giveSpackTx();
		}
	},
	async onReachBottom() {
		uni.showLoading({
			title: '加载中...'
		})
		await this.getMore(); // 下一页
		uni.hideLoading();
		// uni.hideLoading();
	},
	methods: {
		// 加载更多
		async getMore(){
			this.currentPage ++;
			uni.showLoading({
				title: '加载红包列表...',
			})
			var params = {
				"openid": this.$store.state.openid, //openid
				"hbopenid": this.$store.state.hbopenid,
				"projectServerName": 'liaoning',
				"currentPage": this.currentPage,
				"count": this.count
			};
			this.hbListRes = await post('/gifts/queryAllGiftsList', params);
			console.log(this.hbListRes); // reply result replyTime
			var jo = this.hbListRes;
			uni.hideLoading();
			
			if (jo.result.code == '0') {
				if (jo.result.businessCode == '0') {
					this.moneyList = this.moneyList.concat(jo.reply.objList); ////红包列表
					if(this.count > jo.reply.objList.length ){
						this.more = false;
					} else {
						this.more = true;
					}
					console.log(this.moneyList)
					
				} else if (jo.result.businessCode == '2') { //无红包记录
					this.noData = true;
				} else { //businessCode:1失败
					uni.showModal({
						title: '尊敬的用户',
						content: '呜呜，系统开了个小差，请稍后重试！',
					})
				}
			} else { //code!='0'
				uni.showModal({
					title: '尊敬的用户',
					content: '呜呜，系统开了个小差，请稍后重试！',
				})
			}
		},
		// 红包列表
		async hbList() {
			uni.showLoading({
				title: '加载红包列表...',
			})
			var params = {
				"openid": this.$store.state.openid, //openid
				"hbopenid": this.$store.state.hbopenid,
				"projectServerName": 'liaoning',
				"currentPage": this.currentPage,
				"count": this.count
			};
			this.hbListRes = await post('/gifts/queryAllGiftsList', params);
			console.log(this.hbListRes); // reply result replyTime
			var jo = this.hbListRes;
			uni.hideLoading();

			if (jo.result.code === '0') {
				if (jo.result.businessCode === '0') {
					this.restMoney = (Number(jo.reply.totalMoney)).toFixed(2); //账户余额
					this.txnum = (Number(jo.reply.totalMoney)).toFixed(2);
					this.totalMoney = (Number(jo.reply.totalMoney) + Number(jo.reply.giftsMoney)).toFixed(2) //累计红包
					this.moneyList = jo.reply.objList; //红包列表
					if(this.count > jo.reply.objList.length ){
						this.more = false;
					} else {
						this.more = true;
					}
					console.log(this.moneyList)
				} else if (jo.result.businessCode === '2') { //无红包记录
					this.restMoney = '0.00';
					this.totalMoney = '0.00';
					this.noData = true;
				} else { //businessCode:1失败
					uni.showModal({
						title: '尊敬的用户',
						content: '呜呜，系统开了个小差，请稍后重试！',
					})
				}
			} else { //code!='0'
				uni.showModal({
					title: '尊敬的用户',
					content: '呜呜，系统开了个小差，请稍后重试！',
				})
			}
		},

		// 微信手机号授权 获取手机号
		userPhone(e) {
			console.log(e.detail.errMsg)
			if (e.detail.errMsg.lastIndexOf('ok') != -1) { //成功
				// uni.showModal({
				// 	title: '提示',
				// 	content: '手机号授权成功',
				// })
				this.mobileToast = false; //再次授权手机hide
				//解析手机号
				if (this.$store.state.session_key) {
					this.parsePhone(e) //解析手机号
				} else {
					this.$store.dispatch('xcxOpenidGet')
					this.parsePhone(e)
				}
				console.log(this.$store.state.session_key);
			} else { //取消
				this.mobileToast = true
			}
		},
		// 解析手机号
		parsePhone(e) {
			var params = {
				'sessionKey' : this.$store.state.session_key,
				'encryptedData' : e.detail.encryptedData,
				'iv' : e.detail.iv,
				'provinceCode' : 'lnqp'
			};
			return new Promise((resolve, reject) => {
				uni.request({
					url: 'https://xcxact.vjifen.com/api/decrypt',
					method:'POST',
					data: params,
					success: res => {
						console.log(res.data.data.decryptData.phoneNumber);
						var phoneNumber =  res.data.data.decryptData.phoneNumber;
						this.$store.commit('userPhone', phoneNumber); 
						this.savePhone();
						uni.setStorageSync('tel', phoneNumber);
						// 0 false 1 true
						this.hasPhone = phoneNumber == undefined ? 1 : phoneNumber
						console.log(this.hasPhone);
						if(this.hasPhone){ //有值 提现||存储手机号
							//先判断是否有hpopenid
							if(this.restMoney >= 1){
								if (this.$store.state.hbopenid) {
									this.needTx = true;
									this.giveSpackTx(); //提现
								} else {
									this.needTx = true;
									uni.navigateTo({
										url: '../getOpenid/getOpenid?provinceCode=' + 'LN' + '&type=hbopenid',
									})
									return 
								}
							} else {
								this.failToast = true;
							}
						}
					},
					fail: err => {
						this.hasPhone = true;
						
						uni.showModal({
							title: '提示',
							content: '解析手机号结果' + res.data
						})
						reject('decrypt', err)
						console.log(res.data)
					}
				})
			})
		},
		// 保存手机号
		async savePhone() {
			var params = {
				"openid": this.$store.state.openid,
				"projectServerName": 'liaoning',
				"phonenum": this.$store.state.phoneNumber ? this.$store.state.phoneNumber :'' ,
				"isCaptcha": 0
			};
			var savePhoneNumber = await post('/user/updateUserInfoMobile', params);
			console.log('savePhoneNumber');
			// uni.showModal({
			// 	title: 'savePhone 尊敬的用户',
			// 	content: '手机号保存成功'
			// })
		},
		closeMobile() { //  关闭手机号授权弹窗
			this.mobileToast = false;
		},
		// 提现
		async giveSpackTx() {
			if (this.restMoney >= 1) {
				if ( !this.$store.state.hbopenid) {
					console.log(this.$store.state.hbopenid)
					this.needTx = true;
					uni.navigateTo({
						url: '../getOpenid/getOpenid?provinceCode=' + 'LN' + '&type=hbopenid',
					})
					return 
				}
				console.log(this.$store.state.hbopenid)
				console.log(this.needTx);
				uni.showLoading({
					title: '提现中',
				})
				const params = {
					"openid": this.$store.state.openid,
					"hbopenid": this.$store.state.hbopenid,
					"projectServerName": 'liaoning',
				}
				var jo = await post('/gifts/getGiftspack', params);
				console.log(jo);
				uni.hideLoading();
				if (jo.result.code == '0') {
					if (jo.result.businessCode == '0') {
						// uni.showModal({
						// 	title: '尊敬的用户',
						// 	content: '提现成功',
						// })
						this.successToast = true;
						this.restMoney = 0;
					} else if (jo.result.businessCode == '1') { //1
						uni.showModal({
							title: '尊敬的用户',
							content: '您的红包金额不足，再喝几瓶攒够1元发红包！',
						})
					} else if (jo.result.businessCode == '2') { //1
						uni.showModal({
							title: '提示',
							content: '呜呜，系统开了个小差，请稍后重试！',
						})
					} else if (jo.result.businessCode == '4') { //1
						uni.showModal({
							title: '提示',
							content: '提现处理中，请稍后查看详细记录',
						})
					} else if (jo.result.businessCode == '3') { //1
						uni.showModal({
							title: '尊敬的用户',
							content: '根据国家法规对支付服务实名制的要求，请到微信中进行实名认证。实名认证方法：进入【微信】->【我】->【钱包】->【···】->【支付管理】，即可实名认证。',
						})
					} else if (jo.result.businessCode == '-1') { //-1
						uni.showModal({
							title: '提示',
							content: '系统升级中...',
						})
					} else if (jo.result.businessCode == '-2') { //-1
						uni.showModal({
							title: '提示',
							content: '提现操作过于频繁',
						})
					} else if (jo.result.businessCode == '5') { //5
						uni.showModal({
							title: '提示',
							content:jo.result.msg,
						})
					} else {
						uni.showModal({
							title: '提示',
							content: '呜呜，系统开了个小差，请稍后重试！',
						})
					}
				} else if (jo.result.code == '-1') {
					uni.showModal({
						title: '尊敬的用户',
						content: '系统升级中...',
					})
				} else { //code!='0' 服务失败
					uni.showModal({
						title: '尊敬的用户',
						content: '呜呜，服务开了个小差，请稍后重试！',
					})
				}
			} else {
				this.failToast = true;
			}
		},
		
		iknow() {
			this.successToast = false; //弹框消失
			this.hbList();
			// uni.switchTab({
			// 	url:'../mine/mine'
			// })
		},
		// 失败的弹框
		failIknow() {
			this.failToast = false;
		},

	}
}
